<template>
  <div class="d-flex justify-center">
    <!-- 全角色 -->
    <div v-if="item.Chr_Type === 9" class="d-flex mr-1">
      <v-chip
        v-for="(item, index) in ['艾', '柯', '布', '比']"
        :key="index"
        :x-small="xSmall"
        :small="small"
        label
        :class="roleColor[index]"
        class="mr-1"
        text-color="white">
        {{ item }}
      </v-chip>
    </div>

    <!-- 单角色 -->
    <div v-else class="d-flex mr-1">
      <v-chip
        v-if="item.Chr_Type === 0"
        :class="roleColor[0]"
        :x-small="xSmall"
        :small="small"
        label
        text-color="white">
        艾
      </v-chip>

      <v-chip
        v-else-if="item.Chr_Type === 1"
        :class="roleColor[1]"
        :x-small="xSmall"
        :small="small"
        label
        text-color="white">
        柯
      </v-chip>

      <v-chip
        v-else-if="item.Chr_Type === 2"
        :class="roleColor[2]"
        :x-small="xSmall"
        :small="small"
        label
        text-color="white">
        布
      </v-chip>

      <v-chip
        v-else-if="item.Chr_Type === 3"
        :class="roleColor[3]"
        :x-small="xSmall"
        :small="small"
        label
        text-color="white">
        比
      </v-chip>

      <v-chip v-else :x-small="xSmall" :small="small" label text-color="white">未知</v-chip>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    item: {
      required: true,
    },
    xSmall: {
      type: Boolean,
      default: false,
    },
    small: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      roleColor: ['orange lighten-1', 'pink lighten-1', 'red lighten-1', 'purple lighten-1'],
    };
  },
};
</script>
