<template>
  <div class="d-flex align-center w-[500px]">
    <v-tooltip top transition="fade-transition" min-width="240" max-width="240">
      <!-- 图标 -->
      <template v-slot:activator="{ on, attrs }">
        <div
          style="
            margin-right: 10px;
            width: 22px;
            height: 22px;
            border-radius: 4px;
            overflow: hidden;
          "
          v-bind="attrs"
          v-on="on">
          <img
            style="width: 28px; height: 28px; margin-top: -3px; margin-left: -3px"
            :src="`/image/icon/${item.Icon.toString().padStart(5, '0')}.jpg`" />
        </div>
      </template>

      <div class="pa-3">
        <!-- 图标+名称 -->
        <div class="d-flex align-center justify-space-between">
          <!-- 图标+名称 -->
          <div class="d-flex align-center">
            <!-- 图标  -->
            <img
              :src="`/image/icon/${item.Icon.toString().padStart(5, '0')}.jpg`"
              alt=""
              class="mr-2 rounded" />
            <!-- 名称  -->
            <span class="font-weight-medium subtitle-2 white--text">
              {{ item.Name }}
            </span>
          </div>

          <!-- 模板 -->
          <div
            v-if="showTemplateFlag(item)"
            class="ml-2 d-flex align-center justify-center rounded-sm blue-grey darken-4"
            style="width: 14px; height: 14px">
            <span class="cyan--text text--accent-1" style="font-size: 0.8em">T</span>
          </div>
        </div>

        <!-- 分割线 -->
        <v-divider class="my-2 grey darken-3"></v-divider>

        <!-- 属性 -->
        <div class="text--secondary caption">
          <!-- 角色 -->
          <div class="white--text">
            类型：
            <span class="green--text text--lighten-1">
              <span v-for="r in roles" :key="r.type">
                <span v-if="item.Chr_Type === r.type">{{ r.name }}</span>
              </span>

              <span v-if="item.Chr_Type === 9">共同</span>
            </span>
          </div>

          <!-- 部位 -->
          <div class="white--text">
            分类：
            <span class="green--text text--lighten-1">
              <span v-if="item.Type === p.type" v-for="p in parts" :key="p.type">
                {{ p.name }}
              </span>
            </span>
          </div>
        </div>

        <!-- 分割线 -->
        <v-divider v-if="!(item.Type === 11)" class="my-2 grey darken-3"></v-divider>

        <!-- 属性 -->
        <div class="text--secondary caption">
          <!-- 防御力 -->
          <div v-if="item.Def" class="white--text">
            防御力：
            <span
              :class="item.Def < 0 ? 'red--text text--lighten-1' : 'green--text text--lighten-1'">
              {{ calculate(item.Def) }}%
            </span>
          </div>

          <!-- 体力 -->
          <div v-if="item.HP" class="white--text">
            体力：
            <span
              :class="item.HP < 0 ? 'red--text text--lighten-1' : 'green--text text--lighten-1'">
              {{ calculate(item.HP) }}%
            </span>
          </div>

          <!-- 耐力 -->
          <div v-if="item.SP" class="white--text">
            耐力：
            <span
              :class="item.SP < 0 ? 'red--text text--lighten-1' : 'green--text text--lighten-1'">
              {{ calculate(item.SP) }}%
            </span>
          </div>

          <!-- 精神力 -->
          <div v-if="item.MP" class="white--text">
            精神力：
            <span
              :class="item.MP < 0 ? 'red--text text--lighten-1' : 'green--text text--lighten-1'">
              {{ calculate(item.MP) }}%
            </span>
          </div>

          <!-- 爆头率增加 -->
          <div v-if="item.HeadShot_Up" class="white--text">
            爆头率增加：
            <span
              :class="
                item.HeadShot_Up < 0 ? 'red--text text--lighten-1' : 'green--text text--lighten-1'
              ">
              {{ calculate(item.HeadShot_Up) }}%
            </span>
          </div>

          <!-- 伤害范围减少 -->
          <div v-if="item.Range_DMG" class="white--text">
            伤害范围减少：
            <span class="green--text text--lighten-1">{{ calculate(item.Range_DMG) }}%</span>
          </div>

          <!-- 一般移动速度增加 -->
          <div v-if="item.Move_up" class="white--text">
            一般移动速度增加：
            <span class="green--text text--lighten-1">{{ calculate(item.Move_up) }}%</span>
          </div>

          <!-- 移动速度增加 -->
          <div v-if="item.Run_up" class="white--text">
            移动速度增加：
            <span
              :class="
                item.Run_up < 0 ? 'red--text text--lighten-1' : 'green--text text--lighten-1'
              ">
              {{ calculate(item.Run_up) }}%
            </span>
          </div>

          <!-- 子弹数增加 -->
          <div v-if="item.Bullet_ea_UP" class="white--text">
            子弹数增加（以后增加）：
            <span class="green--text text--lighten-1">+{{ item.Bullet_ea_UP }}</span>
          </div>

          <!-- 分割线 -->
          <v-divider class="my-2 grey darken-3"></v-divider>

          <!-- 简介 -->
          <div v-if="item.Explain" class="white--text">
            <span class="caption" v-html="content"></span>
          </div>
        </div>
      </div>
    </v-tooltip>
  </div>
</template>

<script>
import BigNumber from 'bignumber.js';

export default {
  props: ['item', 'roles', 'parts', 'showTemplateFlag'],
  data() {
    return {};
  },
  methods: {
    calculate(n) {
      return new BigNumber(n).multipliedBy(new BigNumber(0.7813)).toFixed(1);
    },
  },

  mounted() {},

  computed: {
    // 简介
    content() {
      const content = this.item.Explain;
      return content.toString().replace(
        /\/b\/c\(.+?\)(.+?)\/c\/b/g,
        '<b style="color: rgba(255,228,000);">$1</b>'
      );
    },
  },
};
</script>
