<template>
  <div>
    <div class="mb-5">
      <!-- 筛选 -->
      <div>
        <!-- 角色 -->
        <v-item-group v-model="selectRole" mandatory class="mb-2">
          <v-item
            v-for="item in roles"
            :key="item.type"
            :value="item.type"
            v-slot="{ active, toggle }"
            class="mr-2">
            <v-btn
              outlined
              small
              :class="active ? 'grey darken-4 white--text' : ''"
              class="px-1"
              @click="toggle">
              {{ item.name }}
            </v-btn>
          </v-item>
        </v-item-group>

        <!-- 部位 -->
        <div class="d-flex justlify-center mb-2">
          <v-item-group v-model="selectPart">
            <v-item
              v-for="item in parts"
              :key="item.type"
              :value="item.type"
              v-slot="{ active, toggle }"
              class="mr-2 mb-2">
              <v-btn
                outlined
                small
                :class="active ? 'grey darken-4 white--text' : ''"
                class="px-1"
                @click="toggle">
                <!-- <v-avatar color="primary" size="24" class="mr-1">
                <img :src="require(`@/assets/images/ui/armor/${tag}.jpg`)" alt="" />
              </v-avatar> -->
                {{ item.name }}
              </v-btn>
            </v-item>
          </v-item-group>
        </div>
      </div>

      <!-- 搜索 -->
      <div class="d-flex">
        <v-text-field
          v-model="keyword"
          solo
          prepend-inner-icon="mdi-magnify"
          hide-details="false"
          placeholder="搜索.."
          clearable
          color="initial"
          style="font-size: 0.8em"></v-text-field>
      </div>
    </div>

    <v-card class="pa-5">
      <v-data-table
        dense
        :header-props="{ sortIcon: 'mdi-menu-up' }"
        :headers="headers"
        :items="pagedList"
        :items-per-page="page"
        :custom-sort="customSort"
        hide-default-footer
        mobile-breakpoint>
        <!-- 名称+图标 -->
        <template v-slot:[`item.Name`]="{ item }">
          <div class="d-flex align-center w-[500px] caption text-no-wrap">
            <!-- 图标 -->
            <tooltip
              :item="item"
              :roles="roles"
              :parts="parts"
              :show-template-flag="showTemplateFlag"></tooltip>
            <!-- 名称 -->
            {{ item.Name }}

            <!-- 模板标记 -->
            <div
              v-if="showTemplateFlag(item)"
              class="ml-1 d-flex align-center justify-center rounded-sm blue-grey darken-4"
              style="width: 12px; height: 12px">
              <span class="cyan--text text--accent-1" style="font-size: 0.7em">T</span>
            </div>
          </div>
        </template>

        <!-- 角色 -->
        <template v-slot:[`item.Chr_Type`]="{ item }">
          <span class="caption">
            <role-chip :item="item" small />
          </span>
        </template>

        <!-- 部位 -->
        <template v-slot:[`item.Type`]="{ item }">
          <span class="caption">
            <part-chip :item="item" small />
          </span>
        </template>

        <!-- 防御力 -->
        <template v-slot:[`item.Def`]="{ item }">
          <span class="caption" v-if="item.Def">
            {{ calculate(item.Def) }}
          </span>
        </template>

        <!-- 体力 -->
        <template v-slot:[`item.HP`]="{ item }">
          <span class="caption" v-if="item.HP">
            {{ calculate(item.HP) }}
          </span>
        </template>

        <!-- 伤害范围减少 -->
        <template v-slot:[`item.Range_DMG`]="{ item }">
          <span class="caption" v-if="item.Range_DMG">
            {{ calculate(item.Range_DMG) }}
          </span>
        </template>

        <!-- 移动速度增加 -->
        <template v-slot:[`item.Run_up`]="{ item }">
          <span class="caption" v-if="item.Run_up">
            {{ calculate(item.Run_up) }}
          </span>
        </template>

        <!-- 子弹数增加 -->
        <template v-slot:[`item.Bullet_ea_UP`]="{ item }">
          <span class="caption" v-if="item.Bullet_ea_UP">
            {{ item.Bullet_ea_UP }}
          </span>
        </template>
      </v-data-table>

      <!-- 分页 -->
      <div v-if="pagedList.length" class="text-center py-2">
        <v-pagination
          color="grey darken-4"
          v-model="currentPage"
          :length="total"
          :total-visible="5"
          @input="changePage"></v-pagination>
      </div>
    </v-card>
  </div>
</template>
<script>
import { mapState, mapGetters, mapActions } from 'vuex';
import _ from 'lodash';
import BigNumber from 'bignumber.js';
import Tooltip from '@/views/armor/components/tooltip.vue';
import RoleChip from '@/components/roleChip.vue';
import PartChip from '@/components/partChip.vue';

export default {
  components: { Tooltip, RoleChip, PartChip },

  data() {
    return {
      roles: [
        {
          name: '艾利克斯',
          type: 0,
        },
        {
          name: '柯莉儿',
          type: 1,
        },
        {
          name: '布莱克',
          type: 2,
        },
        {
          name: '比比',
          type: 3,
        },
      ],

      parts: [
        {
          name: '头盔',
          type: 0,
        },
        {
          name: '军用眼镜',
          type: 1,
        },
        {
          name: '战术手套',
          type: 4,
        },
        {
          name: '战斗服',
          type: 2,
        },
        {
          name: '背包',
          type: 7,
        },
        {
          name: '弹药补充袋',
          type: 3,
        },
        {
          name: '军靴',
          type: 5,
        },
        {
          name: '皮肤',
          type: 11,
        },
        {
          name: '称号',
          type: 10,
        },
        {
          name: '饰品',
          type: 6,
        },
        {
          name: '项链',
          type: 13,
        },
        {
          name: '戒指',
          type: 12,
        },
      ],

      selectRole: 0,

      selectPart: null,

      keyword: '',

      currentPage: 1,

      page: 50,
    };
  },

  computed: {
    ...mapState('armor', ['armorList']),

    headers() {
      return [
        {
          text: '名称',
          value: 'Name',
          align: 'start',
          class: 'caption',
          width: '250',
        },
        {
          text: '角色',
          value: 'Chr_Type',
          align: 'start',
          align: 'center',
          width: '100',
        },
        {
          text: '部位',
          value: 'Type',
          align: 'center',
          class: 'caption',
          width: '100',
        },
        {
          text: '防御力',
          value: 'Def',
          align: 'start',
          class: 'caption',
          width: '80',
        },
        {
          text: '体力',
          value: 'HP',
          align: 'start',
          class: 'caption',
          width: '80',
        },
        {
          text: '伤害范围减少',
          value: 'Range_DMG',
          align: 'start',
          class: 'caption',
          width: '100',
        },
        {
          text: '移动速度增加',
          value: 'Run_up',
          align: 'start',
          class: 'caption',
          width: '100',
        },
        {
          text: '子弹数增加',
          value: 'Bullet_ea_UP',
          align: 'start',
          class: 'caption',
          width: '100',
        },
      ];
    },

    total() {
      return Math.ceil(this.filterList.length / this.page);
    },

    filterList() {
      let list = [];

      // 筛选
      if (this.selectPart === null || this.selectPart === undefined) {
        // 当前角色
        list = this.armorList.filter(
          item => item.Chr_Type === this.selectRole || item.Chr_Type === 9
        );
      } else {
        // 当前角色+部位
        list = this.armorList.filter(item => {
          if (item.Chr_Type === 9) return item.Type === this.selectPart;

          return item.Chr_Type === this.selectRole && item.Type === this.selectPart;
        });
      }

      // 搜索
      if (this.keyword?.trim()) {
        return list.filter(item => {
          return _.includes(item.Name.toString().toLowerCase(), this.keyword.toLowerCase());
        });
      }

      return list;
    },

    pagedList() {
      const start = (this.currentPage - 1) * this.page;

      const end = start + this.page;

      return this.filterList.slice(start, end);
    },
  },

  mounted() {},

  methods: {
    customSort(items, sortBy, sortDesc) {
      if (!sortBy.length) {
        // 默认排序
        return items;
      }

      const [isSortDesc] = sortDesc;
      const [sort] = sortBy; // 排序列字段

      const start = (this.currentPage - 1) * this.page;
      const end = start + this.page;

      if (isSortDesc) {
        // 降序+分页
        return _.sortBy(this.filterList, item => {
          return !item[sort] ? -Infinity : item[sort];
        })
          .reverse()
          .slice(start, end);
      } else {
        // 升序+分页
        return _.sortBy(this.filterList, item => (!item[sort] ? Infinity : item[sort])).slice(
          start,
          end
        );
      }
    },

    changePage(newPage) {
      this.currentPage = newPage;
    },

    calculate(n) {
      // 乘算
      return new BigNumber(n).multipliedBy(new BigNumber(0.78148148148)).toFixed(1);
      // return new BigNumber(n).multipliedBy(new BigNumber(0.7813)).toFixed(1);
    },

    // 是否为模板属性
    showTemplateFlag(item) {
      const weaponConfigs = {
        // 头盔
        helmet: {
          Def: 26,
          HP: 6,
          MP: 3,
          Range_DMG: 4,
          Run_up: 2,
        },
        // 军用眼镜
        goggles: {
          HP: 7,
          MP: 8,
          Range_DMG: 4,
          Move_up: 7,
          Run_up: 9,
        },
        // 战术手套
        gloves: {
          HP: 6,
          MP: 18,
          Range_DMG: 6,
          Move_up: 6,
          Run_up: 6,
        },
        // 战斗服
        combatSuit: {
          Def: 84,
          HP: 8,
          Range_DMG: 27,
          Move_up: 3,
        },
        // 背包
        backpack: {
          Def: 7,
          HP: 6,
          Range_DMG: 30,
          Move_up: 3,
          Run_up: 8,
        },
        // 弹药补充袋
        ammoBag: {
          HP: 12,
          Range_DMG: 7,
          Run_up: 7,
          Bullet_ea_UP: 3,
        },
        // 军靴
        boots: {
          HP: 11,
          MP: 3,
          Range_DMG: 4,
          Run_up: 21,
        },
        // 称号
        title: {
          MP: 6,
          Range_DMG: 6,
          Run_up: 6,
        },
        // 饰品
        accessory: {
          HP: 6,
          Range_DMG: 6,
          Move_up: 6,
        },
        // 戒指
        ring: {
          HP: 6,
          MP: 6,
          Range_DMG: 6,
        },
        // 项链
        necklace: {
          Def: 6,
          Move_up: 6,
          Run_up: 6,
        },
      };

      for (const weapon in weaponConfigs) {
        const config = weaponConfigs[weapon];

        let isMatch = true;

        for (const prop in config) {
          if (item[prop] !== config[prop]) {
            isMatch = false;
            break;
          }
        }

        if (isMatch) {
          return true;
        }
      }

      return false;
    },
  },

  watch: {
    selectPart() {
      this.currentPage = 1;
    },
    selectRole() {
      this.currentPage = 1;
    },
    keyword() {
      this.currentPage = 1;
    },
  },
};
</script>
